@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-5xs: 8px;
  --font-size-3xs: 10px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-smi-4: 12.4px;
  --font-size-lg: 18px;
  --font-size-4xs: 9px;

  /* Colors */
  --color-white: #fff;
  --color-orangered-100: #fd5901;
  --color-orangered-200: rgba(253, 89, 1, 0.15);
  --color-gray-100: #272727;
  --color-gray-200: #232323;
  --color-black: #000;

  /* Gaps */
  --gap-21xl: 40px;

  /* border radiuses */
  --br-8xs: 5px;
  --br-6xs-6: 6.6px;
  --br-4xs-3: 8.3px;
  --br-3xs: 10px;
}
